/* Estilos generales */
.roles-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5vh;
    flex-direction: column;
    background: var(--background-gradient);
    font-family: 'Segoe UI', system-ui, -apple-system, sans-serif;
    background: rgb(26,61,106);
    background: radial-gradient(circle, rgba(26,61,106,1) 0%, rgba(58,141,222,1) 50%, rgba(255,255,255,1) 100%);
    background-size: 300% 300%;
    animation: gradientAnimation 10s ease infinite;
    width: 100vw;
    height: 100vh;
    transform: translateZ(0); 
    will-change: background-position; 
    -webkit-animation: gradientAnimation 10s ease infinite;
    -moz-animation: gradientAnimation 10s ease infinite;
    padding-top: 50px; 
  }
  
  @media (max-width: 480px) {
    .roles-container {
      padding: 1rem;
      border-radius: 1rem;
    }
  }
  
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.1);
    animation: flicker 10s infinite;
  }
  
  @keyframes flicker {
    0%, 100% { opacity: 0.1; }
    50% { opacity: 0.05; }
  }
  
  .nav-container {
    width: 80%;
    top: 0;
    max-width: 600px;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    position: fixed;
    padding: 15px;
    z-index: 1000; 
  }
  
  .roles-card {
    background: rgba(255, 255, 255, 0.95);
    padding: 2vh;
    border-radius: 1.5rem;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
    width: 90%;
    max-width: 500px;
    max-height: 80vh; 
    backdrop-filter: blur(10px);
    transition: box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    text-align: center;
    margin-top: 60px; 
  }
  
  .roles-card:hover {
    box-shadow: 0 12px 40px rgba(0, 0, 0, 0.2);
  }
  
  .roles-card h2 {
    color: #153354;
    font-size: 2rem;
    margin-bottom: 1rem;
    font-weight: 600;
  }
  
  .button-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 1rem;
  }
  
  .primary-btn {
    width: 100%;
    padding: 1rem;
    border: none;
    border-radius: 0.8rem;
    font-size: 1.2rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    background: linear-gradient(135deg, #153354 0%, #3a8dde 100%);
    color: white;
  }
  
  .primary-btn:hover {
    background: linear-gradient(135deg, #102a46 0%, #3a8dde 100%);
    transform: scale(1.02);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  .nav-btn {
    padding: 0.8rem 1.2rem;
    font-size: 1.2rem;
    border-radius: 0.8rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    background: white;
    border: none;
    color: #153354;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    box-sizing: border-box;
    min-height: 50px; 
    width: 100%; 
    flex: 1;
    max-width: 200px;
  }
  
  .nav-btn:hover {
    background: #f0f0f0; 
    transform: scale(1.02);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Tablets (pantallas medianas) */
  @media (max-width: 768px) {
    .roles-card {
      max-width: 90%;
      padding: 1.5rem;
      max-height: 85vh;
    }
  
    .nav-container {
      flex-direction: column;
      gap: 10px;
      height: auto;
      padding: 1rem;
      margin-top: 60px;
    }
    
    .primary-btn {
      width: 100%;
      font-size: 1.1rem;
      padding: 0.9rem;
    }
  }
  
  /* Teléfonos grandes */
  @media (max-width: 480px) {
    .roles-container {
      padding: 1rem;
    }
  
    .roles-card {
      width: 90%;
      max-width: 350px;
      padding: 1.2rem;
    }
  
    .primary-btn {
      font-size: 1.1rem;
      padding: 0.9rem;
    }
  }
  
  @media (max-width: 1024px) {
    .roles-card {
      max-width: 500px;
      padding: 3rem;
    }
  
    .primary-btn {
      font-size: 1rem;
      padding: 0.8rem;
    }
  }